import './Form.css';
import React, { ChangeEvent, FormEvent, useEffect, useState } from 'react';
import axios from 'axios';

type Input = {
    vorname?: string,
    nachname?: string,
    email?: string,
    sonstiges?: string
}

type Output = {
    vorname: string,
    nachname: string,
    email: string,
    sonstiges: string
}

const StartOutput = {
    vorname: "",
    nachname: "",
    email: "",
    sonstiges: ""
}

const Form: React.FC<any> = ({navigate}) => {
    const [error, setError] = useState<any>();
    const [output, setOutput] = useState<Output>(StartOutput);
    const [send, setSend] = useState<string>();
    
    const [success, setSuccess] = useState<any>();
    const [successMessage, setSuccessMessage] = useState<any>();
    
    useEffect(() => {
        if (success){
            setSuccessMessage("Erfolgreich abgesendet!");
        }else{
            setSuccessMessage("Absenden fehlgeschlagen");
        }
        setSend(output?.vorname+output?.nachname+"|"+output?.email+"|"+output?.sonstiges);
    }, [success, output]);

    function handleChange (event: ChangeEvent<HTMLInputElement>) {
        setOutput ((prevState: any) => {
            return {...prevState, [event.target.name]: event.target.value}
        });
    }

    function handleSubmit (event: FormEvent<HTMLFormElement>) {
        event.preventDefault();
        (async () => {
            try {
                if (true) {
                    await axios.put(`${process.env.REACT_APP_SERVER_URL}/submit`,send);
                    setSuccess(true);
                    navigate('/');
                }else {
                    throw new Error("Ungültige Angaben");
                }
            } catch (error) {
                setError(error);
            }
        })();
    }

    return (
        <>
        <div>{error}</div>
        <div>{successMessage}</div>
        <div className='wrapper'>
            <div className='box'>
                <div className='text'>
                    <p>Login</p>
                </div>
           <form onSubmit={handleSubmit}>
              <div className='input-box'>
                  <input
                      type='text'
                      id='vorname'
                      name='vorname'
                      placeholder='Vorname'
                      value={output?.vorname}
                      onChange={handleChange}
                  />
              </div>
              <div className='input-box'>
                  <input
                      type='text'
                      id='nachname'
                      name='nachname'
                      placeholder='Nachname'
                      value={output?.nachname}
                      onChange={handleChange}
                  />
              </div>
              <div className='input-box'>
                  <input
                      type='email'
                      id='email'
                      name='email'
                      placeholder='Email'
                      value={output?.email}
                      onChange={handleChange}
                  />
              </div>
              <div className='input-box'>
                  <input
                      type='textarea'
                      id='sonstiges'
                      name='sonstiges'
                      placeholder='Eigene Vorstellungen'
                      value={output?.sonstiges}
                      onChange={handleChange}
                  />
              </div>
              <div>
                  <button type='submit' className='btn'>Absenden</button>
              </div>
           </form>
           </div>
        </div>
        </>
   );
}

export default Form;