import './Menu.css';

function Menu ({selector, content, onSwitch, id}) {
    return (
        <>
        <div
        onClick={() => onSwitch(id)}
        className='tab'>
            {selector ? <div className='selected'>{content}</div> : <div className='unselected'>{content}</div>}
        </div>
        </>
    );
}



export default Menu;