import Karussell from './karussell.container';
import './karussellWithText.css';
import { motion } from "framer-motion";

function karussellWithText() {
  return (
    
    <div className='flexContainer'>
      <div className='text box'>
        <div>
          <h1>Dies könnte Ihre zukünftige Webseite sein</h1>
        </div>
        <div className='p'>
          Wir haben Ihnen ein paar Beispiele von unseren Designs rausgepickt, 
          die Sie für den Start auswählen können. 
          Bei allem anderen helfen wir Ihnen gerne aus.
        </div>
        <motion.div
        whileTap={{ opacity: 0.8, scale: 1}}
        whileHover={{scale: 1.05}}
        >
        <button className="button">Los geht's</button>
        </motion.div>
      </div>
      <div className='pic box'>
      <Karussell />
      </div>
    </div>
    
  );
}

export default karussellWithText;