import { useState } from 'react';
import Menu from './Menu';
import './Tabs.css'
import { produce } from 'immer';
import Content from './Content';
import tabs from './data.json';


function Tabs () {
    const [title, setTitle] = useState();
    const [content, setContent] = useState();
    const items = tabs;
    const [selector, setSelector] = useState(() => {
        const arr = new Array();
        arr.push(true);
        for (let i = 1; i < items.length; i++) {
            arr.push(false);
        }
        return arr;
    });

    function onSwitch (i) {
        setSelector((prevState) => {
            return produce(prevState, (draftState) => {
                for (let j = 0; j < draftState.length; j++) {
                    if (draftState[j] === true){
                        draftState[j] = false;
                        break;
                    }
                }
                draftState[i] = true;
            });
        })
    }

    return (
        <div className="whole">
            <div className='tabselector'>
                {items.map((e) => (
                    <Menu key={e.id} id={e.id} selector={selector[e.id]} content={e.title} onSwitch={onSwitch} />
                ))}
            </div>
            <div className='content'>
                {items.map((e) => (
                <Content key={e.id} selector={selector[e.id]} text={e.content} />
                ))}
            </div>
        </div>
    );
}

export default Tabs;