import React from "react";

type Props = {
    selector: Array<number>;
    text: Array<string>;
};

const Content: React.FC<Props> = ({selector, text}) => {
    const arr = new Array();
    for (let i = 0; i < text.length; i++) {
        arr.push(i);
    }
    if (selector)return (
        <>
            {
                text.map((e: string) => (
                    <div
                    key={text.indexOf(e)}
                    >
                        {e}
                    </div>
                ))
            }
        </>
    );
}

export default Content