import './Startpage.css';
import Karussell from './DanielsKarussell/karussellWithText';
import Faq from './DanielsFAQ/Faq';
import Tabs from './DanielsTabs/TabsWithText';

function Startpage() {
  return (
    <>
      <div className='main karussell'>
        <Karussell />
      </div>
      <div className='main faq'>
        <Faq />
      </div>
      <div className='main tabs'>
        <Tabs />
      </div>
    </>
  );
}

export default Startpage;
